
@font-face {
  font-family: 'zurich';
  src: url(./static/fonts/zurich.7cda345.ttf);
}
.top {
  width: 100%;
  min-height: 100vh;
  display: grid;
  background-color: black;
  grid-template-rows: 45px 1fr;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1px;
  text-align: center;
  font-size: 12px;
  font-family: Arial,Helvetica,sans-serif;
  letter-spacing: .05em;
}
.top__header.item-link {
    text-decoration: none;
    background-color: white;
    color: black;
}
.top__header.item-link .item-link__text {
      position: relative;
      top: 50%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
      font-size: 22px;
      font-family: zurich;
      font-weight: bold;
      letter-spacing: .1em;
}
.top__contents {
    grid-row: 2;
    grid-column: 1 / 4;
}

